@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'

#head
  .container
    @extend .row
    
    .flex-inner
      @extend .column
      justify-content: flex-start
      &:first-of-type
        width: 30%
      &:last-of-type
        width: 70%
      .logo
        margin-top: 50px
        max-width: 58%
        @extend .column
        .inner
          @extend .column
          align-items: center
          align-self: flex-start
          svg 
            min-width: 85px
            path
              fill: $purple
          .logo-txt
            font-size: 50px
            font-weight: 600
            line-height: 50px
        .txt
          color: $purple
          align-self: flex-end
          font-size: 14px
          font-weight: 400
          margin-top: 8px
      nav 
        margin-top: 70px
        @extend .column
        align-items: flex-start
        a 
          margin-top: 25px
          font-size: 20px
          transition: all 0.5s ease
          &:hover
            color: $purple
      .slick-dots
        @extend .column
        bottom: 20px
        align-items: flex-end
        right: 50px
        width: auto
        li 
          border: 2px solid white
          border-radius: 50%
          margin-top: 10px
          width: 15px
          height: 15px
          transition: all 0.5s ease
          &:hover
            background: white
        .slick-active
          background: white

      .slide
        background: url('../images/img_photo_2.png') no-repeat center center 
        background-size: cover
        flex: 1 1 auto
        min-height: 670px
        @extend .column
        justify-content: center 
        .main 
          margin-left: 7%
          .title
            font-size: 60px
            line-height: 70px
            color: white
            letter-spacing: 1px
            span
              svg 
                max-width: 44px
                max-height: 44px
              &:last-of-type
                svg
                  max-height: 50px
                  max-width: 47px
          .txt
            font-size: 20px
            color: white
            line-height: 30px
            font-weight: 400
#institute
  padding-top: 50px
  .container
    @extend .row
    align-items: flex-start
    .social
      @extend .row
      align-items: flex-end
      a 
        opacity: 0.5
        transition: all 0.5s ease
        margin-right: 40px
        &:hover
          opacity: 1
        svg 
          width: 20px
          height: 20px
    .flex-item:first-of-type
      width: 55%
      .title
        font-size: 30px
        line-height: 40px
        font-weight: 500
      .txt
        @extend .row
        width: 80%
        font-size: 18px
        line-height: 20px
        margin-top: 20px
        font-weight: 400
        div:first-child
          margin-right: 5px
.appriciate
  padding-top: 100px
  .container
    @extend .column
    .title
      font-size: 40px
      line-height: 40px
      font-weight: 600
    .flex-inner
      margin-top: 30px
      @extend .row
      .flex-item
        color: white
        min-height: 350px
        padding: 30px
        .title
          font-size: 30px
        .txt
          margin-top: 40px
          font-size: 18px
          line-height: 25px
      .inner
        width: 50%
        &:first-of-type
          .flex-item         
            &:first-of-type
              background: $blue
            &:nth-of-type(2)
              background: $yellow
            &:last-of-type
              background: $pink
        &:last-of-type
          margin-top: 20px
          .flex-item
            &:first-of-type
              background: $bright-purple
            &:nth-of-type(2)
              background: $green
            &:last-of-type
              background: $dark-blue
          
#programs
  margin-top: 100px
  .container
    @extend .column
    .head
      @extend .row
      align-items: center
      .title
        font-size: 40px
        line-height: 45px
        font-weight: 600
        white-space: nowrap
        svg 
          max-width: 18px
          path 
            fill: black
      .flex-inner
        width: 50%
        @extend .row
        flex-wrap: wrap
        justify-content: flex-start
        .flex-item
          width: 23%
          padding: 10px 0
          font-size: 16px
          font-weight: 500
          text-align: center
          cursor: pointer
          margin-left: 2%
          transition: color 0.5s ease
          &:hover
            color: $green
        .active
          border: 3px solid $green
          color: $green
    .grid
      margin-top: 50px
      @extend .row
      flex-wrap: wrap 
      .deactive
        transform: rotateY(180deg)
        .title
          opacity: 0
        .txt
          opacity: 0
        &:after
          opacity: 0
        .line
          min-width: 120%
          height: 2px 
          background: $green
          transform: rotate(33.5deg)
          position: absolute
          top: 50%
          left: -10%
          opacity: 1 !important
          transition: all 0.5s ease
      .flex-item
        width: 32.5%
        min-height: 250px
        margin-top: 1.3%
        padding: 15px
        cursor: pointer
        transition: all 1s ease
        .title
          font-size: 37px
          line-height: 40px
          font-weight: 500
          transition: all 0.5s ease
        .txt
          font-size: 16px
          line-height: 20px
          margin-top: 4%
          width: 65%
          font-weight: 400
          transition: all 0.5s ease
        .line
          transition: all 0.5s ease
      .green
        border: 10px solid $green
        color: $green
        position: relative
        .line
          opacity: 0
          background: $green

        &:after
          position: absolute
          content: url('../images/img_logo.svg')
          bottom: 2%
          right: 5%
          transform: rotate(90deg)
      .blue
        border: 10px solid $blue
        color: $blue
        position: relative
        .line
          background: $blue
          opacity: 0
          
      .yellow
        border: 10px solid $yellow
        color: $yellow
        position: relative
        .line
          opacity: 0
          background: $yellow

    .button
      align-self: center 
      margin-top: 100px
      background: $dark-blue
      color: white
      text-align: center 
      width: 32.5%
      height: 50px
      cursor: pointer
      font-size: 20px
      line-height: 20px
      font-weight: 400
      display: flex
      span 
        margin: auto
#news
  margin-top: 100px
  .container
    @extend .column
    .title
      font-size: 40px
      line-height: 45px
      font-weight: 600
    .inner
      @extend .row
      .title
        font-size: 24px
        line-height: 30px 
        font-weight: 500
        color: white
      .flex-inner
        width: 31%
        min-height: 1100px
        svg 
          position: absolute
          width: 45px
          bottom: 0
          right: 5%
        @extend .column
        
        .flex-item
          position: relative
          padding: 20px 20px
          margin-top: 12%
          cursor: pointer
          overflow: hidden
          &:hover
            .img
              img 
                transform: scale(1.25)
          .img
            position: absolute
            min-width: 100%
            min-height: 100%
            z-index: -1 
            left: 0
            top: 0
            img 
              transition: all 0.5s ease-in-out
          .title
            font-size: 20px
            line-height: 25px
            width: 80%
        &:first-of-type
          .flex-item
            &:first-of-type
              flex: 9 1 auto
            &:nth-of-type(2)
              flex: 1 1 auto
              position: relative
              padding: 0
              background: transparent
              .txt
                margin-top: 5%
                font-size: 18px
                font-weight: 300
                width: 75%
              .title
                color: $green
                font-size: 24px
                font-weight: 600
                line-height: 30px
                width: 100%
              &:after
                content: url('../images/img_logo.svg')
                position: absolute
                bottom: 0
                right: 5%
                transform: rotate(90deg)

            &:last-of-type
              flex: 7 1 auto
        &:nth-of-type(2)
          .flex-item
            &:first-of-type
              flex: 2 1 auto
            &:nth-of-type(2)
              flex: 2 1 auto
            &:last-of-type
              flex: 2 1 auto
        &:last-of-type
          .flex-item
            &:first-of-type
              flex: 1 1 auto
              position: relative
              padding: 0
              background: transparent
              svg 
                transform: rotate(-90deg)
                path 
                  fill: $yellow
              .txt
                margin-top: 5%
                font-size: 18px
                font-weight: 300
                width: 75%
              .title
                color: $yellow
                font-size: 24px
                font-weight: 600
                line-height: 30px
                width: 100%
            &:nth-of-type(2)
              flex: 5 1 auto
            &:last-of-type
              flex: 1 1 auto
              position: relative
              padding: 0
              background: transparent

              
              .txt
                width: 75%
                margin-top: 5%
                font-size: 18px
                font-weight: 300
              .title
                color: $bright-purple
                font-size: 24px
                font-weight: 600
                line-height: 30px
                width: 100%
              svg 
                path 
                  fill: $bright-purple
#partners
  margin-top: 100px
  .container
    @extend .column
    .title
      font-size: 40px
      line-height: 45px
      font-weight: 600
    .flex-inner
      @extend .row
      flex-wrap: wrap 
      justify-content: center
      align-content: center
      .flex-item
        margin-top: 6%
        width: 20%
        text-align: center

#footer
  margin-top: 100px 
  background: #753F97
  padding: 20px 0
  .container
    @extend .row
    color: white
    a 
      color: white
      &:hover
        text-decoration: underline
    .flex-item
      @extend .column
      justify-content: flex-start
      .title
        font-size: 24px
        font-weight: bold
      .content
        @extend .column
        margin-top: 20px
        a
          margin-top: 5px
          font-size: 14px
          font-weight: 400
    .social
      justify-content: space-between
      span 
        text-decoration: underline
      svg 
        width: 20px
        height: 20px
        opacity: 0.5
        transition: all 0.5s ease
        path 
          fill: white
        &:hover
          opacity: 1
        






      




          