@import 'tools/_mixins'
@import 'tools/_helpers'
@import 'tools/_media_queries'
@import 'tools/_normalize'
@import 'tools/_settings'
#index-two
  #head
    nav 
      a:hover
        color: $yellow
    .logo
      svg
        transform: rotate(90deg)
        path 
          fill: $yellow
      .txt
        color: $yellow
    .slider
      .slide
        background: url('../images/img_photo_1.png') no-repeat center center 
        background-size: cover
        .main 
          .title
            font-size: 70px
            line-height: 90px
  .appriciate
    padding-top: 50px
    .flex-inner
      justify-content: flex-start
      .flex-item
        color: black
        &:first-of-type
          width: 35%
          background: url('../images/img_photo_2.png') no-repeat center center
          background-size: cover
          display: flex
          padding: 0

          .txt
            margin: auto
            width: 50%
            height: 200px
            text-align: justify
            color: white
            font-size: 38px
            line-height: 40px
            font-weight: 500
        &:last-of-type
          width: 57%
          .txt
            margin-top: 2%
            font-size: 16px
            font-weight: 400
            @extend .row
            justify-content: flex-start
            span
              padding-right: 2%
  #programs
    .container
      .flex-inner
        background: $dark-blue
        @extend .row
        .flex-item
          position: relative
          color: white
          padding: 30px 30px 50px 30px
          .more
            color: white 
            text-decoration: underline
            position: absolute
            bottom: 10%
            right: 0
            transform: rotate(-90deg)
            font-size: 16px
          .title
            font-size: 35px
            line-height: 50px
            width: 80%
            font-weight: bold
          .txt
            margin-top: 5%
            font-size: 18px
            line-height: 24px
            font-weight: 300
            width: 70%
            @extend .row
            justify-content: flex-start
            span 
              padding-right: 2%
        &:first-of-type
          .flex-item
            width: 50%  
            &:first-of-type
              background: $green
            &:last-of-type
              background: $violet
      .about
        justify-content: flex-start
        .flex-item
          &:first-of-type
            width: 30%
            .social
              @extend .column
              svg
                max-width: 20px
                max-height: 20px
                margin-top: 20px
                transition: all 0.5s ease
                opacity: 0.5
                &:hover
                  opacity: 1
                path 
                  fill: #fff

          &:last-of-type
            width: 70%
            .title
              width: 70%
            .txt
              width: 50%


        
      .button
        background: $yellow


  #map
    width: 100%
    height: 100%
    min-height: 200px

  #prog 
    padding-top: 100px
    .container
      @extend .column
      .title
        font-size: 40px
        line-height: 40px
        font-weight: 600
      .grid
        margin-top: 50px
        @extend .row
        flex-wrap: wrap 
        .flex-item
          width: 32%
          min-height: 250px
          margin-top: 1.3%
          padding: 15px
          cursor: pointer
          .title
            font-size: 37px
            line-height: 40px
            font-weight: 500
            transition: all 0.5s ease
          .txt
            font-size: 16px
            line-height: 20px
            margin-top: 4%
            width: 65%
            font-weight: 400
            transition: all 0.5s ease
        .green
          border: 10px solid $green
          color: $green
          position: relative
          &:after
            position: absolute
            content: url('../images/img_logo.svg')
            bottom: 2%
            right: 5%
            transform: rotate(90deg)